import React from 'react';
import { ThemeContext } from '../../ContextProvider/ThemeContext';
import styles from './Contact.module.css';

const Contact = () => {
	const { newTheme } = React.useContext(ThemeContext);
	return (
		<footer
			className={styles.footer}
			id='contact'
			style={{ background: `${newTheme.highlightBackground}` }}
		>
			<div className={styles.container}>
				<h1 style={{ color: `${newTheme.title}` }}>Get in Touch</h1>
				<p
					style={{ color: `${newTheme.para}` }}
					className={styles.paragraph}
				>
					I'm actively looking for any new opportunities, in
					full-stack web development.
				</p>
				<div
					style={{ color: `${newTheme.title}` }}
					className={styles.contactOptions}
				>
					<a
						href='tel:+919860136779'
						aria-label='GitHub'
						rel='noreferrer'
						target='_blank'
					>
						
						<img width="30px" src="https://img.icons8.com/color/2x/phone.png"/>
						
					</a>
					<a
						href='mailto: avdhutshinde22@gmail.com'
						aria-label='email'
						target='_blank'
						rel='noreferrer'
					>
						<img width="30px" src="https://img.icons8.com/color/2x/gmail.png"/>
					</a>
					
					<a
						href='https://github.com/avdhut9'
						aria-label='GitHub'
						rel='noreferrer'
						target='_blank'
					>
							<img  width="30px" src="https://img.icons8.com/3d-fluency/2x/github.png" alt="" />
					</a>

					





				

					<a
						href='https://www.linkedin.com/in/avadhut-shinde-0ab276181/'
						aria-label='Linkedin'
						target='_blank'
						rel='noreferrer'
					>
						<img width="30px" src="https://img.icons8.com/fluency/2x/linkedin.png"/>
					</a>
				</div>
				<div>
				<div style={{color:'#5a637a'}}>	mob:+919860136779</div>
				<div style={{color:'#5a637a'}}>	Gmail:avdhutshinde22@gmail.com</div>
				</div>
			</div>
			<div
				style={{ background: `${newTheme.line}` }}
				className={styles.line}
			/>
			<div
				style={{ color: `${newTheme.para}` }}
				className={styles.copyright}
			>
				Designed and build by Avadhut Shinde.
			</div>
		</footer>
	);
};

export default Contact;
